import "./styles.scss";

// favourite Button

let favouriteButton = document.querySelector('.vacancy_favourite');
let vacancyButton = document.querySelector('.vacancy_button');

favouriteButton.addEventListener('click', ()=>{
    favouriteButton.classList.toggle('favouritesave');
    if (vacancyButton.innerHTML ==='Предложить вакансию') {
        vacancyButton.innerHTML = 'Приглашение отправлено &#10003';
    } else {
        vacancyButton.innerHTML = 'Предложить вакансию';
    }
    
})

let favouriteButtonD = document.querySelector('.desktop>.vacancy_favourite');
let vacancyButtonD = document.querySelector('.desktop>.vacancy_button');

favouriteButtonD.addEventListener('click', ()=>{
    favouriteButtonD.classList.toggle('favouritesave');
    if (vacancyButtonD.innerHTML ==='Предложить вакансию') {
        vacancyButtonD.innerHTML = 'Приглашение отправлено &#10003';
    } else {
        vacancyButtonD.innerHTML = 'Предложить вакансию';
    }
    
})

// vacancy menu

let vacancySubmenu = document.querySelector('.vacancysubmenu');

vacancyButton.addEventListener('click', ()=>{
    if (vacancySubmenu.style.display !== 'block'
    ) {
        vacancySubmenu.style.display = 'block';
    } else {
        vacancySubmenu.style.display = 'none';
    }
})

let vacancySubmenuD = document.querySelector('.desktop>.vacancysubmenu');

vacancyButtonD.addEventListener('click', ()=>{
    if (vacancySubmenuD.style.display !== 'block'
    ) {
        vacancySubmenuD.style.display = 'block';
    } else {
        vacancySubmenuD.style.display = 'none';
    }
})


// feedback menu

let feedbackTitle = document.querySelector('.feedback_title');
let feedbackCardGallery = document.querySelector('.feedback_cardgallery');
let feedbackLogo = document.querySelector('.feedback_logo');

feedbackTitle.addEventListener('click', ()=>{
    if (feedbackCardGallery.style.display !== 'none'
    ) {
        feedbackCardGallery.style.display = 'none';
        feedbackLogo.style.transform = 'translateY(5px) rotate(270deg)'
    } else {
        feedbackCardGallery.style.display = 'block';
        feedbackLogo.style.transform = 'translateY(5px) rotate(90deg)'
    }
})

let feedbackTitleD = document.querySelector('.desktop>.feedback_title');
let feedbackCardGalleryD = document.querySelector('.desktop>.feedback_cardgallery');
let feedbackLogoD = document.querySelector('.feedback_logo.desktop');

feedbackTitleD.addEventListener('click', ()=>{
    if (feedbackCardGalleryD.style.display !== 'none'
    ) {
        feedbackCardGalleryD.style.display = 'none';
        feedbackLogoD.style.transform = 'translateY(5px) rotate(270deg)'
    } else {
        feedbackCardGalleryD.style.display = 'block';
        feedbackLogoD.style.transform = 'translateY(5px) rotate(90deg)'
    }
})


// cvmenu popup

let cvmenuFile = document.querySelector('.cvmenu_file');
let popup = document.querySelector('.popup');
let popupButtonClose = document.querySelector('.button_close');

cvmenuFile.addEventListener('click', ()=>{
    popup.style.display = 'block';
})

popupButtonClose.addEventListener('click', ()=>{
    popup.style.display = 'none';
})

let cvmenuFileD = document.querySelector('.desktop>.cvmenu_file');
let popupD = document.querySelector('.desktop>.popup');
let popupButtonCloseD = document.querySelector('.button_close.desktop');

cvmenuFileD.addEventListener('click', ()=>{
    popupD.style.display = 'block';
})

popupButtonCloseD.addEventListener('click', ()=>{
    popupD.style.display = 'none';
})


// portfolio popup

let jobBlockPortfolio = document.querySelector('.job_block_portfolio_gallery');
let popupPortfolio = document.querySelector('.popup_portfolio');
let popupMobilePortfolio = document.querySelector('.popupmobile_portfolio');
let bodyWrapper = document.querySelector('.wrapper');
let popupPortfolioClose = document.querySelector('.popup_portfolio_close');
let popupMobilePortfolioClose = document.querySelector('.popupmobile_portfolio_close');

let popupPortfolioPictBlock = document.querySelector('.popup_portfolio_pictblock');

let popupMobilePortfolioPictBlock = document.querySelector('.popupmobile_portfolio_pictblock');



jobBlockPortfolio.addEventListener('click', (evt)=>{
    console.log(evt.target)
    if (window.innerWidth > 992) {
        popupPortfolio.style.opacity = '100'
        popupPortfolio.style.zIndex = '2'

        popupPortfolioPictBlock.style.background = 'url('+evt.target.src+')'+'center no-repeat';
        popupPortfolioPictBlock.style.backgroundSize = 'contain';
    
        bodyWrapper.style.color = '#000000';
        bodyWrapper.style.opacity = '0.5';
    } else {
        popupMobilePortfolio.style.opacity = '100'
        popupMobilePortfolio.style.zIndex = '2'

        popupMobilePortfolioPictBlock.style.background = 'url('+evt.target.src+')'+'center no-repeat';
        popupMobilePortfolioPictBlock.style.backgroundSize = 'contain';

    
        bodyWrapper.style.color = '#000000';
        bodyWrapper.style.opacity = '0.5';
    }

})

popupPortfolioClose.addEventListener('click', ()=>{

        popupPortfolio.style.opacity = '0'
        popupPortfolio.style.zIndex = '-1'
     
        bodyWrapper.style.backgroundColor = '#ffffff';
        bodyWrapper.style.opacity = '1';
})

popupMobilePortfolioClose.addEventListener('click', ()=>{

        popupMobilePortfolio.style.opacity = '0'
        popupMobilePortfolio.style.zIndex = '-1'
     
        bodyWrapper.style.backgroundColor = '#ffffff';
        bodyWrapper.style.opacity = '1';
})

// gallery in page portfolio 

$('.job_block_portfolio_gallery').slick({
    infinite: true,
    dots: true,
    slidesToShow: 2,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2000
});